/**
 * 
 * @param {String} membership_type 
 * @returns {{membership_type: String, name: String, icon: String, altname: String}}
 */
const findMemberShipType = (membership_type)=>{
    return MembershipTypesList.find((item)=> item.membership_type === membership_type);
}

const getIntervalName = (interval_name) => {
    if(interval_name === 'month'){
        return 'Mensual'
    }else if(interval_name === 'year'){
        return 'Anual'
    }
}

const findSelectedPlanFromPlans = (plans, subscriptions) => {
    let selected_plan = false;
    plans.map((plan)=>{
        if(selected_plan){return;}
        selected_plan = subscriptions.find( // check selected plan.
            (subscription)=> subscription.membership_type === plan.membership_type && subscription.id_plan === plan.id_plan
        );
    })
}

