import { Turpial } from "./turpial";

/** Dispatch event on click outside of nodes */
export function clickOutside(nodes, onClickedOutside, container){
  onClickedOutside = onClickedOutside ?? function(){};
  if(Array.isArray(nodes) === false){
    nodes = [nodes];
  }
  container = container ?? Turpial.$WindowObject.document_root ?? Turpial.$DocumentObject;

  let destroy = function() {
    container.removeEventListener('click', catchClick, true);
  }

  const catchClick = function(event){
    let hasBeenCliked = false;

    nodes.map((node)=>{
      if(typeof node === "string"){
        let new_nodes = [...container.querySelectorAll(node)];
        new_nodes.map((new_node)=>{
          nodes.push(new_node)
        })
      }
    })

    nodes.map(( node )=>{
      if(typeof node === "string"){ return; }
      if(node === null){ return; }
      if(typeof node === "undefined"){ return; }
      if(node.contains(event.target)){
        hasBeenCliked = true;
      }
    })
    
    if(hasBeenCliked === false){
      onClickedOutside(destroy, event.target);
    }
  }
  container.addEventListener("click", catchClick, true);  

  
  /*if(container !== document){    
    document.addEventListener("click", (event)=>{
      console.log(event.target, event.target)
      if(event.target.contains(container) || event.target === container){
        console.log("asdadd");
      }
    });
  }*/


}